  line-height: 21px;
  max-width: 400px; }

@media (max-width: 767px) {
  .connect .content-con h2 {
    text-align: center; } }

.connect .conn-btn {
  /* Только если меньше контейнера */
  margin-bottom: 150px; }
  @media (max-width: 1200px) {
    .connect .conn-btn {
      margin-bottom: calc(50px + 100 * ((100vw - calc(320px)) / 880)); } }
  @media (max-width: 767px) {
    .connect .conn-btn {
      display: flex;
      flex-direction: column;
      align-items: center; } }
  .connect .conn-btn .btn {
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
    max-width: 400px;
    margin-bottom: 40px; }

.connect .money {
  /* Только если меньше контейнера */
  margin-bottom: 150px; }
  @media (max-width: 1200px) {
    .connect .money {
      margin-bottom: calc(50px + 100 * ((100vw - calc(320px)) / 880)); } }

.connect .summ .item-1 {
  /* Только если меньше контейнера */
  margin-bottom: 70px; }
  @media (max-width: 1200px) {
    .connect .summ .item-1 {
      margin-bottom: calc(40px + 30 * ((100vw - calc(320px)) / 880)); } }

.bot-contact .bot-content {
  /* Только если меньше контейнера */
  padding-bottom: 150px; }
  @media (max-width: 1200px) {
    .bot-contact .bot-content {
      padding-bottom: calc(50px + 100 * ((100vw - calc(320px)) / 880)); } }

.info-page {
  color: #FFFFFF;
  background: #161616;
  padding: 100px 0; }
  .info-page h1 {
    /* Только если меньше контейнера */
    font-size: 48px;
    margin-bottom: 40px;
    text-transform: uppercase; }
    @media (max-width: 1200px) {
      .info-page h1 {
        font-size: calc(24px + 24 * ((100vw - calc(320px)) / 880)); } }
  .info-page p {
    max-width: 400px; }

footer {
  background: #161616;
  padding: 70px 0 20px 0; }
  .dark-page footer {
    padding: 20px 0;
    border-top: 1px solid #ffffff; }
  @media (max-width: 767px) {
    footer {
      padding: 30px 0 20px 0; } }

.footer-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px; }
  .dark-page .footer-content {
    display: none; }
  @media (max-width: 1023px) {
    .footer-content {
      flex-direction: column; } }

.footer-top {
  display: flex;
  flex-direction: column;
  padding-right: 30px; }
  @media (max-width: 1023px) {
    .footer-top {
      justify-content: space-between;
      flex-direction: row;
      margin-bottom: 15px; } }
  @media (max-width: 767px) {
    .footer-top {
      justify-content: center; } }
  .footer-top .footer-logo a {
    display: flex;
    margin-bottom: 50px; }
  .footer-top .footer-logo img {
    border-right: 1px solid #ffffff;
    padding-right: 10px;
    width: 40px;
    height: 30px; }
  .footer-top .footer-logo p {
    color: #ffffff;
    max-width: 50px;
    padding-left: 10px; }
  .footer-top .text {
    max-width: 400px; }
    @media (max-width: 767px) {
      .footer-top .text {
        display: none; } }
    .footer-top .text p {
      color: #ffffff; }

.footer-middle {
  display: flex; }
  @media (max-width: 1023px) {
    .footer-middle {
      justify-content: space-around; } }
  @media (max-width: 767px) {
    .footer-middle {
      flex-direction: column; } }
  .footer-middle h3 {
    color: #ffffff;
    margin-bottom: 40px;
    font-size: 14px;
    font-variant: small-caps;
    text-align: start; }
    @media (max-width: 1023px) {
      .footer-middle h3 {
        text-align: center;
        border-bottom: 1px solid #ffffff;
        padding-bottom: 10px; } }
    .footer-middle h3 img {
      vertical-align: 4px;
      padding-right: 10px; }
      @media (max-width: 1023px) {
        .footer-middle h3 img {
          display: none; } }
  .footer-middle .middle-tel, .footer-middle .middle-mail {
    padding-right: 50px; }
    @media (max-width: 1023px) {
      .footer-middle .middle-tel, .footer-middle .middle-mail {
        margin-bottom: 30px;
        text-align: center;
        padding-right: 0; } }
    .footer-middle .middle-tel .tel, .footer-middle .middle-mail .tel {
      margin-bottom: 20px; }
    .footer-middle .middle-tel a, .footer-middle .middle-mail a {
      color: #ffffff; }
  @media (max-width: 1023px) {
    .footer-middle .middle-adress {
      text-align: center; } }
  .footer-middle .middle-adress p {
    color: #ffffff;
    margin-bottom: 20px; }
  .footer-middle .middle-adress address {
    color: #ffffff; }

.footer-bot {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #ffffff;
  padding-top: 20px; }
  .dark-page .footer-bot {
    border: none;
    padding-top: 0; }
  @media (max-width: 899px) {
    .footer-bot {
      flex-direction: column;
      align-items: center; } }
  .footer-bot .link-1 p {
    color: #ffffff; }
    @media (max-width: 899px) {
      .footer-bot .link-1 p {
        margin-bottom: 20px; } }
  @media (max-width: 767px) {
    .footer-bot .link-2 {
      display: flex;
      flex-direction: column;
      align-items: center; } }
  .footer-bot .link-2 a {
    color: #ffffff;
    padding-right: 31px;
    font-variant: small-caps; }
    @media (max-width: 767px) {
      .footer-bot .link-2 a {
        margin-bottom: 10px;
        padding-right: 0; } }
    .footer-bot .link-2 a:last-child {
      padding: 0;
      margin: 0; }
